<template>
  <div class="container">
    <div class="registerBox">
      <div class="unLogin resetPW">
        <div class="doll"><img src="/img/doll.png" /></div>
        <h2>修改密碼</h2>
        <p>請在下方欄位修改您的密碼，下一次登入時，即可使用新密碼登入</p>
        <div class="formRow">
          <h3>密碼 *</h3>
          <input
            id="password1"
            type="password"
            name=""
            value=""
            required
            v-model="User.Password"
            placeholder="請輸入密碼"
          />
        </div>
        <div class="formRow">
          <h3>確認密碼 *</h3>
          <input
            id="password2"
            type="password"
            name=""
            value=""
            required
            v-model="User.Password2"
            placeholder="再次輸入密碼"
          />
        </div>
        <div class="submitBtn">
          <a href="javascript:void(0);" @click="register">設定完成</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      User: {
        Email: "",
        Name: "",
        Password: "",
        Password2: "",
        Mobile: "",
        OpenID: "",
      },
    };
  },
  async mounted() {
    this.setBase("register page");
  },
  methods: {
    async register() {
      if (!this.validate(".registerBox")) {
        return;
      }
      if (this.User.Password != this.User.Password2) {
        alert("兩次密碼不一致，請重新輸入！");
        return;
      }
      if (
        !/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/.test(
          this.User.Password2
        )
      ) {
        alert("密碼設定需包含8位以上的英文大小寫+數字");
        return;
      }
      try {
          this.$api.setToken(this.$route.query.t);
        var result = await this.$api.resetPassword(this.User);
        alert('重設成功，請重新登入');
        this.$router.replace('/login');
      } catch (e) {
        console.error(e);
        alert('重設失敗');
      }finally{
          this.$api.setToken('');
      }
    },
  },
};
</script>